.content-detail{
    padding-top: 80px;
    .title-contact{
        font-family: SVN-SVN-A-Love-Of-Thunder;
        color: #E91A22;
        font-size: 48px;
        text-transform: uppercase;
        margin-bottom: 80px;
        text-align: center;
        @media (max-width: 768px) {
            margin-bottom: 20px;
            font-size: 30px;
        }
    }
    .list-contact{
        text-align: center;
        margin-bottom: 80px;
        span{
            display: block;
            text-align: center;
            margin-bottom: 20px;
            i{
                border-radius: 8px;
                background: #f9df9b;
                color: #A2770D;
                width: 80px;
                height: 80px;
                line-height: 80px;
                text-align: center;
                font-size: 32px;
                border-radius: 50%;
            }
        }
        .title-item-contact{
            color: #625B5B;
            font-family: GoogleSans-Bold;
            margin-bottom: 15px;
        }
        .content-contact{
            color: #0E0202;
        }
        @media (max-width: 768px) {
            margin-bottom: 0;
        }
    }
    @media(max-width: 768px) {
        padding-top: 40px;
    }
}
.content-detail-bottom{
    padding-bottom: 80px;
    form{
        input,textarea{
            background: #fff;
        }
        .btn-contact{
            border-radius: 8px;
            background: var(--primary-primary-500, #E91A22);
            border-radius: 8px;
            overflow: hidden;
            padding: 10px 16px;
            color: #fff;
            i{
                margin-left: 8px;
            }
        }
    }
    @media(max-width: 768px) {
        iframe{
            width: 100%;
            height: 100%;
        }
    }
}
